import { Components, Theme } from '@mui/material';
import { COLORS } from '../colors';
import { cssObject } from '../../lib/styles';

export const buttonTheme: Components<Omit<Theme, 'components'>>['MuiButton'] = {
  defaultProps: {
    disableRipple: true,
  },
  styleOverrides: {
    sizeLarge: {
      minHeight: '50px',
      fontSize: '16px',
      fontWeight: 'bold',
      lineHeight: 1,
      padding: '16px',
      borderRadius: '12px',
      opacity: 1,
    },
    sizeMedium: {
      minHeight: '42px',
      fontSize: '16px',
      fontWeight: 'bold',
      lineHeight: 1,
      padding: '12px',
      borderRadius: '12px',
      opacity: 1,
    },
    sizeSmall: {
      minHeight: '32px',
      fontSize: '14px',
      fontWeight: 'bold',
      lineHeight: 1,
      padding: '8px',
      borderRadius: '12px',
      opacity: 1,
    },
    root: ({ ownerState }) => ({
      minWidth: '10px',
      ...(ownerState.variant === 'primary' &&
        cssObject({
          color: COLORS.WHITE,
          backgroundColor: COLORS.COOL_PURPLE90,
          ':disabled': cssObject({
            color: COLORS.WHITE,
            backgroundColor: COLORS.GRAY40,
            ':hover': {
              backgroundColor: COLORS.GRAY40,
            },
          }),
          ':hover': {
            backgroundColor: COLORS.COOL_PURPLE90,
          },
        })),
      ...((ownerState.variant === 'secondary' ||
        ownerState.variant === 'bordered') &&
        cssObject({
          boxSizing: 'border-box',
          color: COLORS.COOL_PURPLE80,
          backgroundColor: COLORS.COOL_PURPLE10,
          borderColor:
            ownerState.variant === 'bordered'
              ? COLORS.COOL_PURPLE80
              : COLORS.COOL_PURPLE10,
          ':disabled': cssObject({
            color: COLORS.GRAY40,
            backgroundColor: COLORS.GRAY20,
            borderColor:
              ownerState.variant === 'bordered' ? COLORS.GRAY40 : COLORS.GRAY20,
            ':hover': {
              backgroundColor: COLORS.GRAY20,
            },
          }),
          ':hover': {
            backgroundColor: COLORS.COOL_PURPLE10,
          },
          borderStyle: 'solid',
          borderWidth: 1,
        })),
      ...(ownerState.variant === 'icon' &&
        cssObject({
          color: COLORS.COOL_PURPLE90,
          backgroundColor: COLORS.WHITE,
          borderStyle: 'solid',
          borderWidth: 2,
          borderColor: COLORS.COOL_PURPLE20,
        })),
      ...(ownerState.variant === 'icon_gray' &&
        cssObject({
          color: COLORS.GRAY50,
        })),
    }),
  },
};
