import { Box, Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import ScreenSearchDesktopIcon from '@mui/icons-material/ScreenSearchDesktop';
import LockIcon from '@mui/icons-material/Lock';
import LockOpenIcon from '@mui/icons-material/LockOpen';
import { useState } from 'react';
import Flex from '../../UI/atoms/Flex';
import theme from '../../../styles/theme';
import HeaderText from '../../UI/atoms/HeaderText';
import Spacer from '../../UI/atoms/Spacer';
import Text from '../../UI/atoms/Text';

export default function Starter() {
  const navigate = useNavigate();
  const [isLock, setIsLock] = useState(true);

  return (
    <Flex direction="column" center height="100vh">
      <HeaderText h1> ?</HeaderText>
      <Spacer y={12} />
      <Text sm bold color="COOL_PURPLE60">
          
      </Text>
      <Spacer y={24} />
      <Box
        onClick={() => navigate('/searcher')}
        onMouseEnter={() => setIsLock(false)}
        onMouseLeave={() => setIsLock(true)}
      >
        {isLock ? (
          <LockIcon
            sx={{
              width: '60vw',
              height: '50vh',
              color: theme.colors.COOL_PURPLE80,
            }}
          />
        ) : (
          <LockOpenIcon
            sx={{
              width: '60vw',
              height: '50vh',
              color: theme.colors.COOL_PURPLE80,
            }}
          />
        )}
        <Spacer y={12} />
        <Button
          variant="bordered"
          onClick={() => navigate('/searcher')}
          sx={{
            width: '100%',
          }}
        >
           
        </Button>
      </Box>
    </Flex>
  );
}
