import { ChangeEventHandler } from 'react';

export const stringCollapse = <T extends string>(
  value: T,
  length: number,
  compareFunction: (v: T) => boolean = (v) => v.length > length,
  collapse = '...',
  result: string = value,
) => {
  if (compareFunction(value)) {
    return result.slice(0, length) + collapse;
  }
  return value;
};

export const isNumberString = (str: string) => {
  const reg = /^[0-9]*$/;
  return reg.test(str);
};

export const allowOnlyNumber = (str: string) => str.replace(/[^0-9]/g, '');

export const numberToPixel = (value: number) => `${value}px`;

export const handleInput: (
  setFunction: (str: string) => void,
) => ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement> =
  (setFunction) => (e) =>
    setFunction(e.target.value);
