/* eslint-disable no-redeclare */
export function of<T>(): (obj: T) => T;

export function of<T>(obj: T): T;

export function of<T1>(obj1?: T1) {
  return arguments.length !== 0 ? obj1 : <T2>(obj2: T2) => obj2;
}

export const ofMap =
  <T>() =>
  <R extends Record<string, T>>(map: R): R =>
    map;
