import { Box } from '@mui/material';
import { numberToPixel } from '../../../lib/utils';

type SpacerSize = 12 | 16 | 24 | 28 | 32 | 40 | 48 | 52 | 124;

interface SpacerProps {
  x?: SpacerSize;
  y?: SpacerSize;
  direction?: 'horizontal' | 'vertical';
}

export default function Spacer({
  x = 12,
  y = 12,
  direction = 'horizontal',
}: SpacerProps) {
  return (
    <Box
      marginTop={direction === 'horizontal' ? numberToPixel(y) : 0}
      marginLeft={direction === 'vertical' ? numberToPixel(x) : 0}
    />
  );
}
