import { Card, IconButton } from '@mui/material';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import { useSetRecoilState } from 'recoil';
import { EscapeTheme } from '../../../types/escapeTheme';
import Flex from '../../UI/atoms/Flex';
import Text from '../../UI/atoms/Text';
import selectedThemesAtom from '../../../recoil/atoms/selectedThemesAtom';

export default function ThemeCard(theme: EscapeTheme) {
  const escapeTheme = theme;
  const setSelectedThemes = useSetRecoilState(selectedThemesAtom);

  const handleDelete = () => {
    setSelectedThemes((selectedThemes) =>
      selectedThemes.filter(
        (selectedTheme) =>
          !(
            selectedTheme.label === escapeTheme.label &&
            selectedTheme.avgRate === escapeTheme.avgRate
          ),
      ),
    );
  };

  return (
    <Card sx={{ minHeight: '40px' }}>
      <Flex center gap="16px">
        <Text>{`:${escapeTheme.label}`}</Text>
        <Text sx={{ minWidth: '55px' }}>{`:${escapeTheme.avgRate}`}</Text>
        <Text>{`:${escapeTheme.location}`}</Text>
        <IconButton onClick={handleDelete}>
          <RemoveCircleIcon />
        </IconButton>
      </Flex>
    </Card>
  );
}
