import { SerializedStyles, useTheme } from '@emotion/react';
import { SxProps, Theme as MuiTheme } from '@mui/material';
import { CSSObject } from '@mui/system';
import { of } from './object';

export const sx = of<SxProps<MuiTheme>>();

export const cssObject = of<CSSObject>();

export const mergeSx = (
  ...styles: (SxProps<MuiTheme> | undefined)[]
): SxProps<MuiTheme> =>
  styles.map((s) => s ?? {}).flatMap((s) => (Array.isArray(s) ? s : [s]));

export function createStyles<
  T extends Record<
    string,
    | SxProps<MuiTheme>
    | SerializedStyles
    | ((...arg: never[]) => SxProps<MuiTheme>)
    | ((...arg: never[]) => SerializedStyles)
  >,
>(styles: T | ((theme: ReturnType<typeof useTheme>) => T)): () => Readonly<T> {
  return () => {
    const theme = useTheme();
    return typeof styles === 'function' ? styles(theme) : styles;
  };
}
