import { Theme as EmotionTheme } from '@emotion/react';
import { createTheme } from '@mui/material';
import { COLORS } from './colors';
import { buttonTheme } from './theme/buttonTheme';
import { speedDialActionTheme, speedDialTheme } from './theme/speedDialTheme';
import { bottomNavigationActionTheme } from './theme/bottomNavigationTheme';
import { snackbarContentTheme, snackbarTheme } from './theme/snackbarTheme';
import { tabsTheme, tabTheme } from './theme/tabTheme';
import { checkboxTheme } from './theme/checkboxTheme';
import { radioTheme } from './theme/radioTheme';
import { toggleTheme } from './theme/toggleTheme';
import { inputTheme } from './theme/InputTheme';
import { dividerTheme } from './theme/dividerTheme';

const emotionTheme: EmotionTheme = {
  colors: COLORS,
};

const muiTheme = createTheme({
  components: {
    MuiButton: buttonTheme,
    MuiSpeedDial: speedDialTheme,
    MuiSpeedDialAction: speedDialActionTheme,
    MuiBottomNavigationAction: bottomNavigationActionTheme,
    MuiSnackbar: snackbarTheme,
    MuiSnackbarContent: snackbarContentTheme,
    MuiDivider: dividerTheme,
    MuiInput: inputTheme,
    MuiSwitch: toggleTheme,
    MuiRadio: radioTheme,
    MuiCheckbox: checkboxTheme,
    MuiTab: tabTheme,
    MuiTabs: tabsTheme,
  },
  typography: {
    fontFamily: 'Pretendard Variable, sans-serif',
  },
});

const theme = { ...emotionTheme, ...muiTheme };

export default theme;
