import { Theme, Typography } from '@mui/material';
import { useTheme } from '@emotion/react';
import { SxProps } from '@mui/system';
import { Colors } from '../../../styles/colors';
import { mergeSx } from '../../../lib/styles';

export interface TextProps {
  children: string;
  color?: Colors;
  xl?: boolean;
  lg?: boolean;
  md?: boolean;
  sm?: boolean;
  xs?: boolean;
  xss?: boolean;
  bold?: boolean;
  medium?: boolean;
  regular?: boolean;
  underlined?: boolean;
  lineHeight?: string;
  sx?: SxProps<Theme>;
}

export default function Text({
  children,
  color = 'BLACK',
  xl,
  lg,
  md,
  sm,
  xs,
  xss,
  bold,
  medium,
  regular,
  sx,
  underlined,
  lineHeight,
}: TextProps) {
  const theme = useTheme();
  const fontSize =
    (xl && 20) ||
    (lg && 18) ||
    (md && 16) ||
    (sm && 14) ||
    (xs && 12) ||
    (xss && 11) ||
    16;
  const fontWeight =
    (bold && 700) || (medium && 500) || (regular && 400) || 500;
  return (
    <Typography
      sx={mergeSx(
        {
          color: theme.colors[color],
          fontSize,
          fontWeight,
          lineHeight: lineHeight ?? '140%',
          letterSpacing: '-0.025em',
          textDecoration: underlined ? 'underline' : 'none',
          textUnderlinePosition: 'under',
        },
        sx,
      )}
    >
      {children}
    </Typography>
  );
}
