import {
  Autocomplete,
  Button,
  Card,
  IconButton,
  Rating,
  TextField,
} from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import { useState } from 'react';
import { useSetRecoilState } from 'recoil';
import Flex from '../../UI/atoms/Flex';
import theme from '../../../styles/theme';
import Text from '../../UI/atoms/Text';
import { EscapeTheme } from '../../../types/escapeTheme';
import selectedThemesAtom from '../../../recoil/atoms/selectedThemesAtom';

const themes: EscapeTheme[] = [
  { label: 'theme01', avgRate: 1 },
  { label: 'theme02', avgRate: 4 },
  {
    label: 'theme03',
    avgRate: 2.5,
  },
];

export default function PlusCard() {
  const [isAddOn, setIsAddOn] = useState(false);
  const [starValue, setStarValue] = useState<number>(0);
  const [selectedTheme, setSelectedTheme] = useState<EscapeTheme | null>(null);
  const setSelectedThemes = useSetRecoilState(selectedThemesAtom);

  const handleReset = () => {
    setIsAddOn(false);
    setStarValue(0);
    setSelectedTheme(null);
  };

  const handleAdd = () => {
    if (selectedTheme) {
      setSelectedThemes((selectedThemes) => [
        ...selectedThemes,
        {
          label: selectedTheme.label,
          avgRate: starValue,
          location: selectedTheme.location,
        },
      ]);
    }
    handleReset();
  };

  return (
    <Card sx={{ minHeight: '64px' }}>
      <Flex center sx={{ paddingY: '10px' }}>
        {isAddOn ? (
          <Flex direction="row" gap="8px" px={0} center>
            <Autocomplete
              value={selectedTheme}
              onChange={(event, newValue) => {
                setSelectedTheme(newValue);
                setStarValue(newValue?.avgRate ?? 0);
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  variant="standard"
                  sx={{ minWidth: '150px' }}
                />
              )}
              options={themes}
            />
            <Flex px={0} center>
              <Rating
                name=""
                value={starValue}
                onChange={(event, newValue) =>
                  newValue && setStarValue(newValue)
                }
                defaultValue={5}
                precision={0.5}
              />
              <Text sm bold sx={{ minWidth: '33px' }}>{`${starValue}`}</Text>
            </Flex>
            <Button size="small" variant="secondary" onClick={handleReset}>
              
            </Button>
            <Button size="small" variant="primary" onClick={handleAdd}>
              
            </Button>
          </Flex>
        ) : (
          <Flex onClick={() => setIsAddOn(true)} sx={{ width: '100%' }} center>
            <IconButton sx={{ color: theme.colors.BLACK }}>
              <AddCircleIcon />
            </IconButton>
          </Flex>
        )}
      </Flex>
    </Card>
  );
}
